.input-text:not(textarea) {
  height: auto; }

.fusion-header {
  background-position: top right; }

/**/
.fusion-header-v4 .fusion-header {
  padding-top: 0;
  padding-bottom: 0; }

/** MAIN MENU **/
.fusion-secondary-main-menu {
  border-bottom-width: 8px !important; }

.fusion-header-menu-align-center .fusion-main-menu > ul > li > a {
  padding: 0 12px; }

.fusion-main-menu > ul > li > a,
.fusion-vertical-menu-widget ul.menu li a,
.side-nav li a {
  text-transform: uppercase; }

/** HOME **/
.home #content .page {
  width: 100%; }

.home .home-headline-subtext {
  margin-top: 100px !important; }

.home #homepage-textmodules .fusion-content-boxes.content-boxes-clean-horizontal .col {
  padding: 20px 35px 20px 20px;
  border-left: #253C83 2px solid; }
  .home #homepage-textmodules .fusion-content-boxes.content-boxes-clean-horizontal .col .fusion-read-more {
    margin-left: 0 !important; }

.post-content .home-hero-headline h1,
.title .home-hero-headline h1 {
  color: #253C83;
  border-bottom: #253C83 5px solid;
  text-transform: uppercase; }

/* content boxes */
.home-research-areas .fusion-content-boxes.content-boxes-clean-horizontal .col {
  padding-top: 165px;
  padding-right: 10px;
  padding-bottom: 10px; }

.home-research-areas .fusion-content-boxes.content-boxes-clean-horizontal .content-box-column {
  border: none;
  margin: 0 1%;
  background-position: center;
  background-size: cover;
  border-bottom-width: 31px;
  border-bottom-style: solid;
  text-align: right; }

.home-research-areas .fusion-content-boxes.content-boxes-clean-horizontal .content-box-column a {
  text-transform: uppercase;
  color: #fff; }

.home .fusion-content-boxes.content-boxes-clean-horizontal .content-box-column.col-lg-3,
.home .fusion-content-boxes.content-boxes-clean-horizontal .content-box-column.col-md-3,
.home .fusion-content-boxes.content-boxes-clean-horizontal .content-box-column.col-sm-3 {
  width: 23%; }

.home-research-areas .fusion-content-boxes.content-boxes-clean-horizontal .content-box-column:nth-of-type(1) {
  background-image: url("/wp-content/uploads/2019/12/clinical_trials_bkgrd.png");
  border-bottom-color: #9855B4 !important; }

.home-research-areas .fusion-content-boxes.content-boxes-clean-horizontal .content-box-column:nth-of-type(2) {
  background-image: url("/wp-content/uploads/2019/12/data_commons_bkgrd.png");
  border-bottom-color: #EB954A !important; }

.home-research-areas .fusion-content-boxes.content-boxes-clean-horizontal .content-box-column:nth-of-type(3) {
  background-image: url("/wp-content/uploads/2019/12/clinical_research_bkgrd.png");
  border-bottom-color: #535DAB !important; }

.home-research-areas .fusion-content-boxes.content-boxes-clean-horizontal .content-box-column:nth-of-type(4) {
  background-image: url("/wp-content/uploads/2019/12/biologic_research_bkgrd.png");
  border-bottom-color: #D74E78 !important; }

.fusion-read-more::after {
  display: none; }

/** LANDING PAGES **/
.landing-page-title-wrapper {
  height: 192px;
  background-image: url("images/page_title_bkgrd.png");
  background-repeat: no-repeat;
  background-size: cover; }
  .landing-page-title-wrapper h1 {
    display: inline-block;
    margin-top: 2.3em;
    margin-left: 14%;
    background: rgba(136, 56, 165, 0.8);
    color: #fff;
    font-family: "aglet-sans";
    font-weight: 600;
    font-size: 1.8em;
    padding: 0.18em 0.8em; }

.landing-intro-text {
  color: #3558B7;
  font-size: 1.3em;
  line-height: 1.6em; }

/* video */
.video-caption {
  background-color: #D9E3FF;
  margin: 1.0em 0;
  padding: 1.0em 1.8em;
  color: #3558B7;
  font-size: 0.8em; }

.video-caption a {
  color: #DE3470;
  font-size: 120%; }

/** Staff list **/
.timeline-align .content-box-column > .col {
  display: flex;
  align-items: center; }
  .timeline-align .content-box-column > .col .heading {
    margin-top: 0 !important; }

.magic-timeline .fusion-content-boxes.fusion-columns-1 .fusion-column {
  margin-bottom: 70px; }

.magic-timeline .content-box-column > .col .content-container {
  padding-left: 12px !important; }

.light-blue-boxes .content-box-column > .col .content-container {
  background: #D9E3FF; }

.light-blue-boxes .text-container {
  background: #D9E3FF; }

.affiliates-timeline .content-box-column > .col .content-container {
  padding-left: 40px !important;
  padding-right: 100px;
  width: 600px;
  height: 144px;
  display: table;
  margin-top: 30px; }
  .affiliates-timeline .content-box-column > .col .content-container h4 {
    vertical-align: middle;
    display: table-cell; }

.leadership-layout .light-blue-boxes .text-container {
  font-size: 0.8em;
  color: #3558B7; }

.leadership-layout h4 {
  margin-top: 0;
  margin-bottom: 5px; }

.consortium-layout .light-blue-boxes .consortium-container {
  padding: 12px 30px 10px; }

.exec-layout .light-blue-boxes {
  display: flex;
  align-items: center; }
  .exec-layout .light-blue-boxes .exec-container {
    padding: 30px 70px 30px 30px;
    margin-left: 20px; }

.exec-layout .exec-margin {
  margin-bottom: 70px; }

/** GLOBAL **/
.fusion-header {
  background-position: right center !important; }

a:hover {
  color: #DE3470 !important; }

body.has-sidebar #main {
  padding-top: 50px; }

body.has-sidebar #content {
  width: 80%;
  padding-left: 70px;
  padding-top: 30px;
  border-left: #233985 2px solid; }

body.has-sidebar .fusion-page-title-bar .fusion-page-title-captions {
