  margin-left: 14%; }

h1, h2 {
  text-transform: uppercase; }

body #main .post-content .fusion-border-below-title h2, body #main h2 {
  padding-left: 60px;
  line-height: 2em;
  color: #8838A5;
  background: url("/wp-content/uploads/2020/01/h2_graphic.png") 0 50% no-repeat; }

body #main .post-content .fusion-border-below-title h2, body #main h2 {
