.fusion-footer-widget-area .footer-menu-and-legal {
  color: #fff; }
  .fusion-footer-widget-area .footer-menu-and-legal a {
    color: #fff;
    text-decoration: underline; }

.fusion-footer-widget-area .footer-menu-and-legal .footer-menu {
  float: right; }

.fusion-footer-widget-area .footer-menu-and-legal .footer-menu li {
